<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step=2">Next</li>
      <li v-if="step == 2" @click="publish">Add</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <h4>안녕 {{ $store.state.name}}</h4>
  <button @click="$store.commit('이름변경')">버튼</button>

  <h4>나이 {{ $store.state.age}}</h4>
  <button @click="$store.commit('증가', 10)">버튼</button>
  <button @click="증가(20)">증가하기</button>

  <p>{{ $store.state.more}}</p>
  <button @click="$store.dispatch('getData')">더보기</button>

  <p>{{now()}} {{카운터}}</p>
  <p>{{now2}} {{카운터}}</p>
  <button @click="카운터++">날짜갱신</button>

  <p>{{name}}</p>
  <p>{{이름}}</p>



  <Container @write="작성한글 = $event" :업로드이미지="업로드이미지" :게시물="게시물" :step="step"/>

  <button @click="moreData">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>


</template>
<script>
  import postdata from './assets/postdata'
  import Container from './components/Container.vue'
  import axios from 'axios'
  import {mapMutations, mapState} from 'vuex'

  export default {
    name: 'App',
    data() {
      return {        
        게시물: postdata,
        step: 0,
        업로드이미지: '',
        작성한글 : '',
        선택한필터: '',
        카운터: 0
      }
    },

    mounted(){
      this.emitter.on('필터적용', (a)=>{
        console.log(a);
        this.선택한필터 = a;
      })
    },
    computed : {
      now2(){
        return new Date()
      },
      
      ...mapState(['name', 'age', 'likes']),
      ...mapState({이름: 'name', 나이: 'age', 좋아요:'likes'})
    }, 


    methods: {
      now(){
          return new Date()
      },
      
      ...mapMutations(['setMore', '증가']),

      moreData() {

        axios.get('https://tarolong.github.io/more0.json').then((res) => {
          // 요청 성공시 실행할 코드
          console.log(res.data);
          this.게시물.push(res.data);
        });
      },

      upload(e){
        let temp = e.target.files
        // console.log(temp[0].type);

        let url = URL.createObjectURL(temp[0]);
        this.업로드이미지 = url;
        this.step = 1;
      },
      publish(){
        let 내게시물 = {
          name: "Minny",
          userImage: "https://placeimg.com/100/100/animals",
          postImage: this.업로드이미지,
          likes: 49,
          date: "Apr 4",
          liked: false,
          content: this.작성한글,
          filter: this.선택한필터
        };
        this.게시물.unshift(내게시물);
        this.step = 0;
      }

    },

    components: {
      Container
    }
  }
</script>

