import axios from 'axios'
import { createStore } from 'vuex'

const store = createStore({
  state(){
    return {
      name: '홍길동',
      age: 20,
      likes: 30,
      likesClick : false,
      more: {}
    }
  },

  // 1. 데이터 수정방법 정의 하는 곳
  mutations: {
    setMore(state, payload){
      state.more = payload;
    },

    이름변경(state) {
      state.name = '김길동'
    },
    증가(state, payload){
      state.age += payload
    },

    좋아요(state){
      if(state.likesClick == false){
        state.likes++;
        state.likesClick = true
      }else{
        state.likes--;
        state.likesClick = false
      }      
    }
  },

  actions: {
    getData(context){
      axios.get('https://tarolong.github.io/more0.json').then((res) => {
          // 요청 성공시 실행할 코드
          console.log(res.data);
          context.commit('setMore', res.data);
        });
    }
  },


})

export default store