<template>
    <div @click="fire" class="filter-item" :class="필터" :style="{backgroundImage:`url(${이미지})`}">
        <slot name="filter"></slot>
    </div>
</template>
<script>
    export default {
        name: 'FilterBox',        
        props: {
            이미지: String,
            필터: String
        },
        methods: {
            fire(){
                this.emitter.emit('필터적용', this.필터)
            }
        }
    }
</script>
<style>
    .filter-item {
        width: 100px;
        height: 100px;
        margin: 10px 10px 10px auto;
        padding: 8px;
        display: inline-block;
        color: white;
        background-size: cover;
        background-position: center;
    }
</style>