<template>
    <div>
        <div v-if="step==0">
            <Post v-for="(item, index) in 게시물" :key="index" :게시물="item" />
        </div>
        <div v-if="step==1">
            <!-- 필터선택페이지 -->
            <div :class="선택한필터" class="upload-image" :style="{backgroundImage:`url(${업로드이미지})`}"></div>

            <div class="filters">

                <!-- <FilterBox v-for="(item, index) in 필터들" :key="index" :이미지="업로드이미지" :class="item"></FilterBox> -->
                <FilterBox v-for="(item, index) in 필터들" :key="index" :이미지="업로드이미지" :필터="item">
                    <template v-slot:filter><span>{{item}}</span></template>
                </FilterBox>

            </div>

        </div>
        <div v-if="step==2">
            <!-- 글작성페이지 -->
            <div :class="선택한필터" class="upload-image" :style="{backgroundImage:`url(${업로드이미지})`}"></div>
            <div class="write">
                <textarea @input="$emit('write', $event.target.value)" class="write-box">write!</textarea>
            </div>
        </div>

    </div>
</template>
<script>
    import Post from './Post.vue'
    import FilterBox from './FilterBox.vue'

    export default {
        name: 'Container',
        data() {
            return {
                필터들: ["aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson",
                    "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua",
                    "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"
                ],

                선택한필터 : '',
            }
        },
        

        mounted(){
            this.emitter.on('필터적용', (a)=>{
                console.log(a);
                this.선택한필터 = a;
            })
        },
        props: {
            게시물: Array,
            step: Number,
            업로드이미지: String,
        },
        components: {
            Post,
            FilterBox
        }
    }
</script>
<style>
    .upload-image {
        width: 100%;
        height: 450px;
        background: cornflowerblue;
        background-size: cover;
    }

    .filters {
        overflow-x: scroll;
        white-space: nowrap;
    }

    .filter-1 {
        width: 100px;
        height: 100px;
        background-color: cornflowerblue;
        margin: 10px 10px 10px auto;
        padding: 8px;
        display: inline-block;
        color: white;
        background-size: cover;
    }

    .filters::-webkit-scrollbar {
        height: 5px;
    }

    .filters::-webkit-scrollbar-track {
        background: #f1f1f1;
    }

    .filters::-webkit-scrollbar-thumb {
        background: #888;
        border-radius: 5px;
    }

    .filters::-webkit-scrollbar-thumb:hover {
        background: #555;
    }

    .write-box {
        border: none;
        width: 90%;
        height: 100px;
        padding: 15px;
        margin: auto;
        display: block;
        outline: none;
    }
</style>